import React, { useState, useEffect, useCallback } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Button,
  CircularProgress,
  Grid,
  Divider,
  Table,
  TableBody,
  TableRow,
  TableCell,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
} from '@mui/material';
import { useAuth } from '../contexts/AuthContext';
import apiService, { baseStaticURL } from '../services/api';

// Helper function to parse content string
const parseContent = (content) => {
    const data = {};
    if (content) {
        // 
        const pairs = content.split(/[,\n]/); // 
        pairs.forEach(pair => {
            const parts = pair.split(':');
            if (parts.length >= 2) {
                const key = parts[0].trim(); // 
                const value = parts.slice(1).join(':').trim(); // 
                if (key) { // 
                   data[key] = value;
                }
            }
        });
    }
    console.log('Parsed content data:', data); // 
    return data;
};

// Function to calculate countdown (copied from Notification.js)
const calculateCountdown = (purchaseTime, period) => {
    if (!purchaseTime || !period) return 'N/A';
    
    const purchaseDate = new Date(purchaseTime);
    const expiryDate = new Date(purchaseDate.getTime() + period * 24 * 60 * 60 * 1000);
    const now = new Date();
    const diff = expiryDate - now;

    if (diff <= 0) {
      return '';
    }

    const days = Math.floor(diff / (1000 * 60 * 60 * 24));
    const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
    const seconds = Math.floor((diff % (1000 * 60)) / 1000);

    return `${days}${hours}${minutes}${seconds}`;
  };

function Detail() {
  const { id } = useParams();
  console.log('[Detail.js] Received ID from params:', id); // LOG ID
  const navigate = useNavigate();
  const { isAuthenticated } = useAuth();
  const [post, setPost] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [isPaymentPasswordSet, setIsPaymentPasswordSet] = useState(false);
  const [isPaymentPasswordModalOpen, setIsPaymentPasswordModalOpen] = useState(false);
  const [paymentPassword, setPaymentPassword] = useState('');
  const [paymentLoading, setPaymentLoading] = useState(false);
  const [paymentError, setPaymentError] = useState('');
  const [contentData, setContentData] = useState({}); // State to hold parsed content data
  const [countdown, setCountdown] = useState(''); // State to hold countdown value

  // Backend base URL for images
  // const backendBaseUrl = 'https://fw108ck86325.vicp.fun'; // REMOVE hardcoded URL

  const checkPaymentPasswordStatus = useCallback(async () => {
    try {
      const response = await apiService.paymentPasswordService.getPaymentPasswordStatus();
      if (response.success) {
        setIsPaymentPasswordSet(response.data.hasPayPassword);
      }
    } catch (err) {
      console.error(':', err);
    }
  }, []);

  const loadPostDetails = useCallback(async () => {
    setLoading(true);
    setError('');
    console.log(`[Detail.js] loadPostDetails called for ID: ${id}`); // LOG CALL
    try {
      const response = await apiService.postService.getPostDetails(id);
      console.log('[Detail.js] API getPostDetails response:', response); // LOG API RESPONSE
      if (response && response.data) { // Ensure response and response.data exist
        setPost(response.data);
        console.log('[Detail.js] Set post state:', response.data); // LOG POST STATE
        await checkPaymentPasswordStatus();
        if (response.data.content) {
            const parsed = parseContent(response.data.content);
            setContentData(parsed);
            console.log('[Detail.js] Set contentData state:', parsed); // LOG PARSED CONTENT
        }
      } else {
        console.error('[Detail.js] Invalid API response structure for getPostDetails:', response);
        setError(': ');
        setPost(null); // Ensure post is null on error
      }
    } catch (err) {
      console.error('[Detail.js] Error in loadPostDetails:', err);
      setError(err.message || '');
      setPost(null); // Ensure post is null on error
    } finally {
      setLoading(false);
    }
  }, [id, checkPaymentPasswordStatus]); // Added checkPaymentPasswordStatus to dependencies

  useEffect(() => {
    loadPostDetails();
  }, [loadPostDetails]);

  // Effect to update countdown every second if purchased
  useEffect(() => {
    let timer;
    if (post && post.isPurchased) {
      timer = setInterval(() => {
        setCountdown(calculateCountdown(post.purchaseTime, post.period));
      }, 1000);
    } else {
      setCountdown(''); // Clear countdown if not purchased
    }

    return () => clearInterval(timer);
  }, [post]); // Re-run effect if post object changes

  const handleContact = () => {
    if (!isAuthenticated) {
      navigate('/login');
      return;
    }
    if (post?.isPurchased) {
        console.log('Already purchased, implement contact logic');
        return;
    }

    if (!isPaymentPasswordSet) {
        navigate('/set-payment-password');
        return;
    }

    setIsPaymentPasswordModalOpen(true);
  };

  const handlePaymentPasswordSubmit = async () => {
      setPaymentLoading(true);
      setPaymentError('');
      try {
          const response = await apiService.postService.purchaseInfo(id, { paymentPassword });

          if (response.success) {
              console.log(':', response.message);
              setPost(prevPost => ({ ...prevPost, isPurchased: true }));
              setIsPaymentPasswordModalOpen(false);
              setPaymentPassword('');
              setPaymentError('');
              navigate('/operation-result', {
                state: {
                  success: true,
                  message: response.message || '',
                  context: 'payment',
                  redirectTo: `/detail/${id}` // Optional: where to go back
                }
              });
          } else {
              console.error(':', response.message);
              setPaymentError(response.message || '');
              if (response.message.includes('')) {
                  setIsPaymentPasswordModalOpen(true);
              } else {
                  setIsPaymentPasswordModalOpen(false);
              }
          }

      } catch (err) {
          console.error(':', err);
          setPaymentError(err.response?.data?.message || err.message || '');
          setIsPaymentPasswordModalOpen(false);
      } finally {
          setPaymentLoading(false);
      }
  };

  const handlePaymentPasswordClose = () => {
      setIsPaymentPasswordModalOpen(false);
      setPaymentPassword('');
      setPaymentError('');
  };

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Box sx={{ p: 3 }}>
        <Typography color="error" align="center">
          {error}
        </Typography>
      </Box>
    );
  }

  if (!post) {
    return (
      <Box sx={{ p: 3 }}>
        <Typography align="center"></Typography>
      </Box>
    );
  }

  return (
    <Box sx={{ bgcolor: '#f7f8fa', minHeight: '100vh' }}>
      <Box sx={{ maxWidth: 800, mx: 'auto', mt: 3, p: 2 }}>
        <Card>
          <CardContent>

            {/*  */}
            {!post.isPurchased ? (
              <Box>
                {/*  */}
                {post.imageUrls && post.imageUrls.length > 0 && (
                  <Box sx={{ mb: 2 }}>
                    <Box
                      component="img"
                      src={`${baseStaticURL}${post.imageUrls[0]}`}
                      alt={post.title}
                      sx={{
                        width: '100%',
                        height: 'auto',
                        objectFit: 'contain',
                      }}
                    />
                  </Box>
                )}
                <Typography variant="h6" gutterBottom>
                  {post.title} {/*  */}
                </Typography>
                <Typography variant="body1" color="text.secondary" sx={{ mb: 1 }}>
                  : {post.loanAmount ? post.loanAmount.toFixed(2) : '--'}
                </Typography>
                 <Typography variant="body1" color="text.secondary" sx={{ mb: 1 }}>
                  : {post.period ? `${post.period}` : '--'}
                </Typography>
                <Typography variant="body1" color="text.secondary" sx={{ mb: 2 }}>
                  : {post.repaymentAmount ? `${post.repaymentAmount}` : '--'}
                </Typography>

                <Divider sx={{ my: 2 }} />

                <Typography variant="body1" color="text.primary" align="center" sx={{ mt: 2, fontWeight: 'bold' }}>
                  
                </Typography>

                {/*  */}
                 <Box sx={{ mt: 3, display: 'flex', justifyContent: 'center' }}>
                  <Button variant="contained" color="primary" onClick={handleContact} disabled={paymentLoading}>
                    {paymentLoading ? <CircularProgress size={24} /> : ` ${post.loanAmount} `}
                  </Button>
                </Box>
                 {paymentError && (
                  <Typography color="error" align="center" sx={{ mt: 2 }}>
                    {paymentError}
                  </Typography>
                )}

              </Box>
            ) : (
              <Box>
                {/*  */}

                {/* 1.  */}
                {post.imageUrls && post.imageUrls.length > 0 && (
                  <Box sx={{ mb: 2 }}>
                    <Box
                      component="img"
                      src={`${baseStaticURL}${post.imageUrls[0]}`}
                      alt={post.title}
                      sx={{
                        width: '100%',
                        height: 'auto',
                        objectFit: 'contain',
                        borderRadius: 1
                      }}
                    />
                  </Box>
                )}

                <Typography variant="h6" gutterBottom>
                  {post.title} {/*  */}
                </Typography>

                {/* 2.  */}
                <Table size="small" sx={{ mb: 2 }}>
                  <TableBody>
                    {/*  */}

                    {/*  */}
                    <TableRow>
                      <TableCell component="th" scope="row" sx={{ width: '30%', fontWeight: 'bold' }}>:</TableCell>
                      <TableCell>{post.title}</TableCell>
                    </TableRow>

                    {/*  */}
                    {contentData[''] && (
                      <TableRow>
                        <TableCell component="th" scope="row" sx={{ fontWeight: 'bold' }}>:</TableCell>
                        <TableCell>{contentData['']}</TableCell>
                      </TableRow>
                    )}

                    {/*  */}
                    {contentData[''] && (
                      <TableRow>
                        <TableCell component="th" scope="row" sx={{ fontWeight: 'bold' }}>:</TableCell>
                        <TableCell>{contentData['']}</TableCell>
                      </TableRow>
                    )}

                    {/*  */}
                    {contentData[''] && (
                      <TableRow>
                        <TableCell component="th" scope="row" sx={{ fontWeight: 'bold' }}>:</TableCell>
                        <TableCell>{contentData['']}</TableCell>
                      </TableRow>
                    )}

                    {/*  */}
                    {contentData[''] && (
                      <TableRow>
                        <TableCell component="th" scope="row" sx={{ fontWeight: 'bold' }}>:</TableCell>
                        <TableCell>{contentData['']}</TableCell>
                      </TableRow>
                    )}

                    {/*  */}
                    {contentData[''] && (
                      <TableRow>
                        <TableCell component="th" scope="row" sx={{ fontWeight: 'bold' }}>:</TableCell>
                        <TableCell>{contentData['']}</TableCell>
                      </TableRow>
                    )}

                    {/*  */}
                     {post.loanAmount !== undefined && post.loanAmount !== null && (
                      <TableRow>
                        <TableCell component="th" scope="row" sx={{ fontWeight: 'bold' }}>:</TableCell>
                        <TableCell>{post.loanAmount.toFixed(2)}</TableCell>
                      </TableRow>
                    )}

                    {/*  */}
                    {post.repaymentAmount !== undefined && post.repaymentAmount !== null && (
                       <TableRow>
                         <TableCell component="th" scope="row" sx={{ fontWeight: 'bold' }}>:</TableCell>
                         <TableCell>{post.repaymentAmount}</TableCell>
                       </TableRow>
                    )}

                    {/*  */}
                    {post.period !== undefined && post.period !== null && post.period > 0 && (
                      <TableRow>
                        <TableCell component="th" scope="row" sx={{ fontWeight: 'bold' }}>:</TableCell>
                        <TableCell>{post.period}</TableCell>
                      </TableRow>
                    )}

                    {/*  */}
                     {post.isPurchased && post.period > 0 && post.expiryTime && countdown !== '' && (
                         <TableRow>
                           <TableCell component="th" scope="row" sx={{ fontWeight: 'bold' }}>:</TableCell>
                           <TableCell>
                             {countdown === '' ? (
                                 <Typography variant="body2" color="error" fontWeight="bold" component="span">
                                     
                                 </Typography>
                             ) : (
                                 <Typography variant="body2" color="primary.main" fontWeight="bold" component="span">
                                     {countdown}
                                 </Typography>
                             )}
                           </TableCell>
                         </TableRow>
                       )}

                  </TableBody>
                </Table>

                 {/* 3.  () */}
                {post.imageUrls && post.imageUrls.length > 1 && (
                  <Box sx={{ mt: 2 }}>
                    <Typography variant="subtitle2" gutterBottom>:</Typography>
                    <Grid container spacing={1}>
                      {post.imageUrls.slice(1).map((url, index) => (
                        <Grid item xs={12} sm={6} md={4} key={index}>
                          <Box
                            component="img"
                            src={`${baseStaticURL}${url}`}
                            alt={`Image ${index + 2}`}
                            sx={{
                              width: '100%',
                              height: 'auto',
                              objectFit: 'contain',
                              borderRadius: 1
                            }}
                          />
                        </Grid>
                      ))}
                    </Grid>
                  </Box>
                )}

                {/*  () */}
                 {/*  content  */}
                {/*  content  */}
                {/* content */}
                 {/* <Typography variant="body1" color="text.primary" sx={{ whiteSpace: 'pre-wrap', mt: 2 }}>
                   {post.content}
                </Typography> */}

                 {/*  */}
                 <Box sx={{ mt: 3, display: 'flex', justifyContent: 'center' }}>
                   <Button variant="contained" color="primary" onClick={handleContact}>
                      
                    </Button>
                 </Box>

              </Box>
            )}

          </CardContent>
        </Card>

        {/* Payment Password Modal */}
        <Dialog open={isPaymentPasswordModalOpen} onClose={handlePaymentPasswordClose}>
          <DialogTitle></DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              label=""
              type="password"
              fullWidth
              variant="outlined"
              value={paymentPassword}
              onChange={(e) => setPaymentPassword(e.target.value)}
              error={!!paymentError}
              helperText={paymentError}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handlePaymentPasswordClose} color="secondary" disabled={paymentLoading}>
              
            </Button>
            <Button onClick={handlePaymentPasswordSubmit} color="primary" disabled={paymentLoading || !paymentPassword}>
              {paymentLoading ? <CircularProgress size={24} /> : ''}
            </Button>
          </DialogActions>
        </Dialog>
      </Box>
    </Box>
  );
}

export default Detail; 