import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import apiService from '../services/api';

const AuthContext = createContext(null);

// Helper function to ensure user object has _id
const normalizeUserData = (userData) => {
  if (!userData) return null;
  const normalizedUser = { ...userData };
  if (userData.id && !userData._id) {
    normalizedUser._id = userData.id;
  }
  // If _id exists but id doesn't, can also add id for consistency if needed elsewhere,
  // but primary goal here is to ensure _id for internal app use.
  // if (userData._id && !userData.id) {
  //   normalizedUser.id = userData._id;
  // }
  return normalizedUser;
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [error, setError] = useState(null);
  const [unreadCSCount, setUnreadCSCount] = useState(0);

  const fetchUnreadCSCount = useCallback(async () => {
    if (!isAuthenticated) {
      console.log('AuthContext: fetchUnreadCSCount skipped - not authenticated');
      return;
    }
    try {
      const response = await apiService.customerService.getUnreadCount();
      if (response.success && response.data && typeof response.data.unreadCount === 'number') {
        setUnreadCSCount(response.data.unreadCount);
      } else {
        console.log('AuthContext: Invalid response format:', response);
      }
    } catch (err) {
      console.error('AuthContext: Failed to fetch unread CS message count:', err);
    }
  }, [isAuthenticated]);

  useEffect(() => {
    // token
    const token = localStorage.getItem('token');
    const storedUser = localStorage.getItem('user');
    
    if (token && storedUser) {
      try {
        let userData = JSON.parse(storedUser);
        userData = normalizeUserData(userData); // Normalize on load
        if (userData && userData._id) { // Check for _id after normalization
          setUser(userData);
          setIsAuthenticated(true);
          apiService.customerService.getUnreadCount()
            .then(response => {
              if (response.success && response.data && typeof response.data.unreadCount === 'number') {
                setUnreadCSCount(response.data.unreadCount);
              }
            })
            .catch(err => console.error('AuthContext: Initial fetch unread CS count failed:', err));
        } else {
          console.error('localStorage_id:', userData);
          alert(`: _id`);
          localStorage.removeItem('token');
          localStorage.removeItem('user');
          setIsAuthenticated(false);
        }
      } catch (err) {
        console.error(':', err);
        alert(`: ${err.message}`);
        localStorage.removeItem('token');
        localStorage.removeItem('user');
        setIsAuthenticated(false);
      }
    } else {
        setIsAuthenticated(false);
    }
    setLoading(false);
  }, []);

  useEffect(() => {
    let intervalId;
    if (isAuthenticated) {
      fetchUnreadCSCount();
      intervalId = setInterval(fetchUnreadCSCount, 30000);
    }
    return () => {
      if (intervalId) {
        clearInterval(intervalId);
      }
    };
  }, [isAuthenticated, fetchUnreadCSCount]);

  const updateUser = (newUserData) => {
    const normalizedUser = normalizeUserData(newUserData); // Normalize before setting
    if (normalizedUser) {
      localStorage.setItem('user', JSON.stringify(normalizedUser));
      setUser(normalizedUser);
    }
  };

  const login = async (credentials) => {
    setLoading(true);
    setError(null);
    console.log('AuthContext - ');

    try {
      console.log('AuthContext -  apiService.userService.login');
      const response = await apiService.userService.login(credentials);
      console.log('AuthContext - :', response);

      // Ensure response exists and contains expected structure from apiService.userService.login
      // We expect response here to be the data object from the server directly, e.g., { success: true, token: '...', user: {...} }
      if (response && response.success) { // Check for success field as returned by userService.login
        const { token, user } = response;
        
        if (token && user) {
          console.log('AuthContext -  Token  User ');
          
          // Normalize user data if necessary (e.g., ensure _id)
          const normalizedUser = normalizeUserData(user); // Use the helper to normalize

          localStorage.setItem('token', token);
          localStorage.setItem('user', JSON.stringify(normalizedUser));
          setIsAuthenticated(true);
          setUser(normalizedUser);
          console.log('AuthContext -  true');
          console.log('AuthContext - :', normalizedUser);

        } else {
          console.error('AuthContext - :  token  user');
          setError('');
          setIsAuthenticated(false);
          setUser(null);
        }
      } else {
         // Handle cases where response is received but success is false or format is unexpected
         console.error('AuthContext - :', response?.message || '', response);
         setError(response?.message || '');
         setIsAuthenticated(false);
         setUser(null);
      }

    } catch (error) {
      console.error('AuthContext -  ( API ):', error);
      // Error message should ideally be set by the api.js catch block
      setError(error.message || '');
      setIsAuthenticated(false);
      setUser(null);

      // Specific error handling based on structure from api.js error throw
      if (error.response && error.response.data && error.response.data.message) {
           setError(error.response.data.message);
      } else if (error.message && (error.message.includes('') || error.message.includes('') || error.message.includes(''))) {
          // Error message already set by api.js
      } else if (error.message === 'Network Error') {
         setError('');
      } else if (error.code === 'ECONNABORTED') { // Axios timeout
         setError('');
      }
    } finally {
      setLoading(false);
      console.log('AuthContext -  loading  false');
    }
  };

  const register = async (userData) => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.userService.register(userData);
      localStorage.setItem('token', response.token);
      // updateUser will handle normalization
      updateUser({ ...response.user, token: response.token });
      setIsAuthenticated(true);
      setLoading(false);
      return { success: true, data: response };
    } catch (err) {
      console.error(':', err);
      setError(err.message || '');
      setIsAuthenticated(false);
      setLoading(false);
      localStorage.removeItem('token');
      localStorage.removeItem('user');
      return { success: false, error: err.message || '' };
    }
  };

  const logout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    setIsAuthenticated(false);
    setUser(null);
    setError(null);
  };

  const updateUserProfile = async (profileData) => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.userService.updateProfile(profileData);
      // Assuming response.data contains the updated user fields
      // Normalize the incoming data and merge with existing user (if any, to preserve token etc.)
      const updatedFields = normalizeUserData(response.data); 
      updateUser({ ...user, ...updatedFields }); // user here should already be normalized
      setLoading(false);
      return { success: true, data: response.data };
    } catch (err) {
      console.error(':', err);
      setError(err.message || '');
      setLoading(false);
      return { success: false, error: err.message || '' };
    }
  };

  const isAdmin = () => {
    return user && user.role === 'admin';
  };

  const value = {
    user, // This will now be the normalized user object with _id
    loading,
    isAuthenticated,
    error,
    unreadCSCount,
    fetchUnreadCSCount,
    login,
    register,
    logout,
    updateUserProfile,
    isAdmin,
    updateUser, // Exposing the normalized updateUser
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}; 